h1 {
  color: #ffee58
}

.contentWrapper {
  display: grid;
  grid-template-columns: 1fr 3fr;
  background-color: white;
  margin: 30px 30px 30px 30px;
  border-radius: 10px;
  font-family: 'Press Start 2P', cursive;
  color: rgba(0,0,0,.54)
}

@media screen and (max-width: 780px) {
  .contentWrapper {
    display: flex;
    flex-direction: column;
  }
  .itemDescriptionWrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.contentWrapper img {
  margin: 20px 70px;
  border-radius: 10px;
  max-height: 420px;
}


.itemDescription ul {
  list-style-type: none;
  text-align: left;
}

.itemDescription li {
  margin-top: 10px;
}

h1 {
  color: rgba(0,0,0,.87);
}

